<template>
<div class="container">
	<div class="row">
		<div class="col-6">
			<div class="row">
				<div class="col-2">
					<label for="">Monto</label>
				</div>
		        <div class="col-10">
					<input v-model.number="Monto" type="number">
			    </div>
				<div class="col-2">
					<label for="">Plazo</label>
				</div>
				<div class="col-10">
					<select v-model="Plazo" @change="Gracia=0">
						<option :value="6">6 meses</option>
						<option :value="12">12 meses</option>
						<option :value="18">18 meses</option>
						<option :value="24">24 meses</option>
						<option :value="36">36 meses</option>
						<option :value="48">48 meses</option>
						<option :value="60">60 meses</option>	
					</select>
				</div>
				<div class="col-2">
					<label for="">Tasa</label>
				</div>
				<div class="col-10">
					<select v-model="Tasa">
						<option :value="0.17">17.0000%</option>
						<option :value="0.18">18.0000%</option>
						<option :value="0.19">19.0000%</option>
						<option :value="0.20">20.0000%</option>
						<option :value="0.21">21.0000%</option>
						<option :value="0.22">22.0000%</option>	
					</select>
				</div>
				<div class="col-2">
				<label for="">Gracia</label>
			</div>
				<div class="col-10">
				<select v-model="Gracia">
					<option :value="3" v-if="Plazo>3" >3 meses</option>
					<option :value="6" v-if="Plazo>6" >6 meses</option>
					<option :value="12" v-if="Plazo>12" >12 meses</option>
					<option :value="18" v-if="Plazo>18" >18 meses</option>
					<option :value="24" v-if="Plazo>24" >24 meses</option>	
				</select>
				</div>

				<div class="col-2">
					<label for="">Fecha Inicial</label>
				</div>
				<div class="col-10">
					<input v-model="Fecha_inicial" type="date">
				</div>
		
			</div>		
		</div> 

		<div class="col-6">
			<div class="row">
				<div class="col-4">
					<label>Pago Promedio</label> 
				</div>
				<div class="col-8">
					<p class="mb-0">{{Promedio}}</p>
				</div>
				<div class="col-4">
					<label>Pago Maximo</label>
				</div>
				<div class="col-8">
					<p class="mb-0">{{max}}</p>
				</div>
				<div class="col-4">
					<label>Pago Minimo</label>
				</div>
				<div class="col-8">
					<p class="mb-0">{{min}}</p>
				</div>
				<div class="col-4">
					<label >Intereses Totales</label>
				</div>
				<div class="col-8">
					<p class="mb-0">{{Interes_t}}</p>
				</div>
			</div>
		</div>
	</div>


			<button @click="Generar_tabla">Generar</button>
		<div class="row">
			<div class="col-10">
				<div class="row align-items-center text-center">
					<div class="col-auto">N°</div>
					<div class="col">Mes</div>
					<div class="col">Dispersión</div>
					<div class="col">Principal</div>
					<div class="col">Interés</div>
					<div class="col">Pago</div>
					<div class="col">Saldo</div>
					<div class="col">Días Cobro Interés</div>
				</div>
				<div class="row align-items-center text-center" v-for="(Item,i) in Formulas">
					<div class="col-auto">{{Item.Numero}}</div>
					<div class="col">{{Item.Fecha}}</div>
					<div class="col">{{i==0?"$ "+Item.Dispersion:""}}</div>
					<div class="col">{{`$ ${Item.Principal}`}}</div>
					<div class="col">{{"$ "+Item.Interes}}</div>
					<div class="col">{{"$ "+Item.Pago}}</div>
					<div class="col">{{"$ "+Item.Saldo}}</div>
					<div class="col">{{Item.Dias_Cobro}}</div>
				</div>
			</div>
			
		</div> 
	</div>

</template>
<script type="text/javascript">
	export default{
		data(){

			return{
				Monto:10000,
				Plazo:12,
				Tasa:0.17,
				Gracia:0,
				Fecha_inicial:"2021-01-01",
				Formulas:[],
				Promedio:0,
				max:0,
				min:0,
				Interes_t:0


			}
		},

		
		methods:{
			Generar_tabla(){
					this.Formulas=[]
					let Fecha=this.$moment(this.Fecha_inicial,"YYYY-MM-DD"),
					promedio=0
					this.Interes_t=0
					this.max=0
					this.min=0
					
					
				for (var i = 0; i <= this.Plazo; i++) {
					const dispersion=i==0?this.Monto:0,
					principal=i==0?0:Math.round((this.Gracia>=i?0:(i==0?0:this.Monto/(this.Plazo-this.Gracia)))* 100) / 100,
					saldo= i==0?dispersion-principal:(this.Formulas[i-1].Saldo-principal+dispersion)
					let Fecha_anterior=Fecha.clone()
					saldo =Math.round (saldo*100)/100
					const dias_cobro=i==0?0:Fecha.diff(Fecha_anterior.subtract(1, "months"),'days'),
					interes=i==0?0:Math.round((i==0?0:this.Formulas[i-1].Saldo*((this.Tasa/360)*dias_cobro))*100)/100,
					pago =Math.round((i==0?0:principal+interes)*100)/100
					this.max = pago>this.max?pago:this.max
					this.min= i==1 || pago<this.min?pago:this.min
					this.Interes_t= Math.round ((this.Interes_t + interes)*100)/100
					//if (i>0)
					//console.log(`${this.Formulas[i-1].Saldo}*((${this.Tasa}/360)*${dias_cobro}))*100/100`)
					this.Formulas[i]= {

						Numero: i,
						Fecha: Fecha.locale("es").format("MMM YYYY"),
						Dispersion: dispersion,
						Principal: principal,
						Interes: interes,
						Saldo: i==this.Plazo?0:saldo,
						Dias_Cobro:dias_cobro,
						Pago: pago


					}
					promedio=promedio+this.Formulas[i].Pago
					Fecha.add(1,"months")
				}
				console.log(promedio,(this.Formulas.length-1))
				this.Promedio=Math.round(promedio/(this.Formulas.length-1))
				
			}
		},
		//watch:{
			//Plazo:function(uno,dos){
				//console.log(uno,dos)	
				//this.Gracia=0
				//console.log(this.Gracia)

			//},

		//}

	}
</script>